import React, { useState } from 'react';
import AboutMe from './pages/AboutMe/AboutMe';
import MyWork from './pages/MyWork/MyWork';
import UlButtons from './small/UlButtons';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faGithub } from '@fortawesome/free-brands-svg-icons';
import { faBars } from '@fortawesome/free-solid-svg-icons';
import './App.css'
import './css/SideBar.css';
import './css/icons.css';
import './css/mediaQueries.css'

const pages = {
    aboutMe: {
        name: 'About Me',
        component: AboutMe,
    },
    myWork: {
        name: 'My Work',
        component: MyWork,
    },
};

function App() {
    const [currentPage, setCurrentPage] = useState('aboutMe')
    const CurrentComponent = pages[currentPage].component;
    const [navMenuDisplay, setDisplay] = useState(false)

    const ToggleDisplay = () => {
        navMenuDisplay ? setDisplay(false) : setDisplay(true);
    }

    return (
        <div className="container">
            <div className="sidebar">
                <FontAwesomeIcon icon={faBars} className="fa-3x nav-icon" onClick={ToggleDisplay} />
                <ul className={'cybr-ul ' + (navMenuDisplay ? ' showNavMenu' : ' hiddeNavMenu')}>
                    <UlButtons handlerFunction={(event) => {setCurrentPage(event.target.getAttribute('name'))}} />
                    <div className="icons">
                        <FontAwesomeIcon icon={faGithub} className="fa-3x" />
                    </div>
                </ul>
            </div>
            <CurrentComponent />
        </div>
    )
}

export default App;
